@import "components/variables";
@import "components/generales/accordion";
@import "components/generales/banner-dudas";

.content-accordion {
  li {
    color: $gray-siroco;
    span {
      font-weight: 800;
    }
  }
  a {
    color: $gray-siroco;
    text-decoration: none;
    word-break: break-all;
    transition: all 0.3s;
    &:hover {
      font-weight: 800;
      color: $blue-siroco;
    }
  }
}